@import "lib/bourbon/bourbon", "lib/neat/neat";
@import "variables";
@import "../../../node_modules/mapbox-gl/dist/mapbox-gl.css";

/*
    Map styles
*/
.mapboxgl-popup {
    max-width: 400px;
    font: 12px/20px 'Helvetica Neue', Arial, Helvetica, sans-serif;
}

@each $aside, $bside in (top, bottom), (right, left), (bottom, top), (left, right) {
    .mapboxgl-popup-anchor-#{$aside} {
        .mapboxgl-popup-tip {
            border-#{$bside}-color: $pale-green;
        }
    }
}
.mapboxgl-popup-content {
    background-color: $pale-green;
}
.mapboxgl-popup-close-button {
    color: $serious-green;
}
.marker-title {
    font-weight: 700;
}
.map-container {
    @include fill-parent;
    .mapboxgl-map {
        overflow: visible;
    }
}
.search-map {
    position: relative;
    background-color: $pale-green;
    .search-controls {
        @include position(absolute, 2% 1.5% null null);
        @include size(33% auto);
        box-shadow: 0 0 5px rgba(51, 51, 51, 0.2);
        input {
            @include fill-parent;
            padding: 8px;
        }
    }
}

.popup-content-inner {
    width: 220px;
    padding: 0 10px;

    ul {
        margin: 0;
        li {
            margin-bottom: 6px;
            padding-bottom: 6px;
            border-bottom: 1px solid $dark-night;
            &:last-of-type {
                margin: 0;
                border-bottom: none;
            }
        }
    }
}
